const options = [
    { label:'Livestream', value: "livestream" },
    { label: 'Video review, Pr', value: "pr" },
    { label: 'Chp hnh qung co, Lookbook', value: "lookbook"},
    { label:'Chp Feedback', value: "feedback" },
    { label:'Tr l (Photographer, Ln  tng,...)', value: "troly" },
    { label: 'Khc', value: "khc" },
    { label: 'Tng hp', value: "th", disabled: true },
  ];

  const options2 = [
    { label:'TP H Ch Minh', value: "livestream" },
    { label: 'H Ni', value: "pr" },
    { label: ' Nng', value: "lookbook"},
    { label:'Hu', value: "feedback" },
    { label:'Cn Th', value: "troly" },
    { label: 'Qung Ninh', value: "khc" },
    { label: 'Nha Trang', value: "th", disabled: true },
  ];





const Example = () => {
  const [selected, setSelected] = useState([]);
  const [selected2, setSelected2] = useState([]);

  return (
    <div>
      <h1>Lnh vc</h1>
      <pre>{JSON.stringify(selected)}</pre>
      <div style={{ width:"400px", color:"succsess"}}>
      <MultiSelect
        options={options}
        value={selected}
        onChange={setSelected}
        labelledBy="Lnh vc"
      />
      </div>
      <br/>

      <h1>Thnh ph</h1>
      <pre>{JSON.stringify(selected2)}</pre>
      <div style={{ width:"400px", color:"succsess"}}>
      <MultiSelect
       hasSelectAll={("hasSelectAll", false)}
        options={options2}
        value={selected2}
        onChange={setSelected2}
        labelledBy="Lnh vc"
      />
      </div>
    </div>
  );
};

export default Example;