import '../../../../node_modules/bootstrap/dist/css/bootstrap.min.css';
import "./Register.css"
import * as React from 'react';
import { useState } from "react";
import { Navigate, Link } from 'react-router-dom';
import { DOMAIN_API } from '../../../config/const';

import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';

export default function Login({ socket, setIsLogined, navigate }) {
    const [name, setFullname] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [brandname, setBrandname] = useState('');
    const [gender, setGender] = useState('');
    const [address, setAddress] = useState('');
    const [phone, setPhone] = useState('');
    const [emailErrorFormat, setEmailErrorFormat] = useState('');
    const [errorPassword, setErrorPassword] = useState('');
    const [errorFullname, setErrorFullname] = useState('');
    const [canRegister, setCanRegister] = useState('');
    const [canLogin, setCanLogin] = useState(false);

    const [provinces, setProvince] = useState([]);

    async function getAllProvince() {
        try {
          let url1 = "";
          url1 = DOMAIN_API + `list-province-vn`;
          await fetch(url1, {
            method: "GET",
            headers: {
              'Content-Type': 'application/json',
            },
          })
            .then(res => res.json())
            .then(
              (result) => {
                setProvince(result);
              }
            )
        }
        catch (error) {
          console.log("error: ", error)
        }
        finally {
        }
      }

    function handleChangeBrandname(event) {
        setBrandname(event.target.value)
    }
    function handleChangePassword(event) {
        setPassword(event.target.value)
    }
    function handleChangeFullname(event) {
        setFullname(event.target.value);
    }
    function handleChangeEmail(event) {
        setEmail(event.target.value);
    }
    function handleChangeGender(event) {
        setGender(event.target.value);
    }
    function handleChangeAddress(event) {
        setAddress(event.target.value);
    }
    function handleChangePhone(event) {
        setPhone(event.target.value);
    }
    function invalidEmail(email) {
        var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if(email.match(mailformat)){
            let flag = true;
            fetch(DOMAIN_API+`is-available-email`,{
                method: "POST",
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ email })
            })
            .then(res => res.json())
            .then(
                (result)=>{
                    if(result == true) {
                        setEmailErrorFormat('');
                    }else{
                        setEmailErrorFormat('Email khng hp l!');
                        setCanRegister('ng k khng thnh cng');
                        return false;
                    }
                }
            )
        }else{
            return false;
        }
    }
    function invalidPassword(password) {
        if(password === ''){
            window.alert("Bn cha nhp Mt khu!")
            setErrorPassword("Bn cha nhp password");
            setCanRegister('ng k khng thnh cng');
            return 0;
        }
        else{
            if(password.length < 8 || password.length > 25){
                window.alert("Mt khu cha ng yu cu. Mt khu phi di 8 n 25 k t!")
                setErrorPassword("Mt khu phi di 8 n 25 k t");
                setCanRegister('ng k khng thnh cng');
                return 0;
            }
        }
        return 1;
    }
    function invalidFullname(fullname) {
        if(fullname === ''){
            window.alert("Cha nhp H v tn!")
            setErrorFullname("Bn cha nhp h tn");
            setCanRegister('ng k khng thnh cng');
            return 0;
        }
        else{
        }
        return 1;
    }
    async function handleSubmit(event) {
        event.preventDefault();
        setCanRegister('');
        let flag_mail  = await invalidEmail(email);
        if (flag_mail === false) {
            window.alert("Email khng hp l!")
            setEmailErrorFormat('Email khng hp l!');
            setCanRegister('ng k khng thnh cng');
        }
        let flag_password = invalidPassword(password);
        if (flag_password == 1){
            setErrorPassword('')
        }
        let flag_name = invalidFullname(name);
        if (flag_name == 1){
            setErrorFullname('');
        }
        let fullname = name;
        if(brandname =='' )
        window.alert("Cha nhp tn nhn hng!")
        if(gender =='' )
        window.alert("Cha chn gii tnh!")
        if(address =='' )
        window.alert("Cha chn a ch Tnh/ Thnh ph!")

        fetch(DOMAIN_API+`is-available`,{
            method: "POST",
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ fullname, email, password, phone, address, gender, brandname })
        })
        .then(res => res.json())
        .then(
            (result)=>{
                if(result == true){
                    
                    if (fullname !== '' && password !== '' &&  email!=='' ) {
                        const url = DOMAIN_API + "register-brands";
                        const requestOptions = {
                            method: 'POST',
                            headers: { 'Content-Type': 'application/json' },
                            body: JSON.stringify({fullname, email, password, phone, address, gender, brandname })
                        };
                        fetch(url, requestOptions)
                        .then(res => res.json())
                        .then((result) => {
                            if(result == true){
                                window.alert("ng k thnh cng!")
                                setCanLogin(true);
                            }
                        })
                        .catch(error => console.log('Form submit error', error))
                    }
                    
                }
                else{
                   setCanRegister('ng k khng thnh cng')
                }
            }
        )
            
        
    };

    React.useEffect(() => {
        getAllProvince();
      }, [])

    if(canLogin){
        return (
            <Navigate to="/brand-login"/>
        )
    }
    if (localStorage.access_token){
        socket?.disconnect();
        localStorage.removeItem("access_token");
        return (
            <Navigate to="/brand-register"/>
        );
    }
    return (
        <div>
            <div className='back-ground'></div>
            <div className='back' >
            <Link to="/brand-login" style={{ fontSize: "16px", color: "white",
            textDecoration: "none", cursor:"pointer"  }}> 
            Quay li</Link>
            </div>
            <div className='title'  style={{fontWeight:"600", fontSize: "30px", color: "white" }} >
                ng k ti khon
            </div>
            <div className='paper'>
                <div className='regulation'>
                    <div style={{ paddingTop: "20px", paddingBottom: "20px", paddingLeft: "40px", paddingRight: "40px" }}>
                        <div style={{ fontSize: "18px", fontWeight: "bold" }} >
                            Quy nh
                        </div>
                        <div style={{ marginTop: "20px" }}>
                             m bo cht lng dch v, KOLsMarketing khng cho php mt ngi dng to nhiu ti khon khc nhau.
                            <br />
                            Nu pht hin vi phm, KOLsMarketing s ngng cung cp dch v ti tt c cc ti khon trng lp hoc chn ton b truy cp ti h thng website ca KOLsMarketing.
                            <br />
                            i vi trng hp khch hng  s dng ht 3 tin tuyn dng min ph, KOLsMarketing h tr kch hot ng tin tuyn dng khng gii hn sau khi qu doanh nghip cung cp thng tin giy php kinh doanh.
                        </div>
                        <div style={{ paddingTop: "20px" }}>
                            <p > <span> Bn  c ti khon?</span>
                                <Link to="/brand-login" style={{ color: "#00B14F", textDecoration: "none" }}> ng nhp ngay</Link>
                            </p>
                        </div>
                    </div>
                </div>

                <div>
                    <form>
                        <div style={{ paddingTop: "20px", paddingBottom: "20px", paddingLeft: "40px", paddingRight: "40px" }}>
                            <div style={{ fontSize: "18px", fontWeight: "bold" }} >
                                TI KHON
                            </div>
                            <br />
                            <Grid container spacing={2}>
                                <Grid item xs={4}>
                                    Email ng nhp *
                                </Grid>
                                <Grid item xs={8} className="center-parent">
                                    <input 
                                        type="email" 
                                        className="form-control" 
                                        id="email" 
                                        aria-describedby="emailHelp" 
                                        placeholder="Email"
                                        value={email}
                                        onChange = {handleChangeEmail} />
                                </Grid>
                                <Grid item xs={4}>
                                    S in thoi *
                                </Grid>
                                <Grid item xs={8} className="center-parent">
                                    <input 
                                        type="text" 
                                        className="form-control" 
                                        id="phone" 
                                        aria-describedby="emailHelp" 
                                        placeholder="S in thoi" 
                                        value={phone}
                                        onChange = {handleChangePhone}/>
                                </Grid>
                                <Grid item xs={4}>
                                    Mt khu *
                                </Grid>
                                <Grid item xs={8} className="center-parent">
                                    <input 
                                        type="password"
                                        name="password"
                                        id="password"
                                        className="form-control"
                                        placeholder="Mt khu (T 8 n 25 k t)"
                                        value={password}
                                        onChange = {handleChangePassword}
                                    />
                                </Grid>
                            </Grid>
                            <div style={{ paddingTop: "30px" }}>
                                <div style={{ fontSize: "18px", fontWeight: "bold" }} >
                                    THNG TIN NHN HNG, NH TUYN DNG
                                </div>
                                <br />
                                <Grid container spacing={2}>
                                    <Grid item xs={4}>
                                        H v tn *
                                    </Grid>
                                    <Grid item xs={8} className="center-parent">
                                        <input 
                                        type="text" 
                                        className="form-control" 
                                        id="fullname" 
                                        aria-describedby="emailHelp" 
                                        placeholder="H tn" 
                                        value={name}
                                        onChange = {handleChangeFullname}
                                    />
                                    </Grid>
                                    <Grid item xs={4}>
                                        Gii tnh *
                                    </Grid>
                                    <Grid item xs={8} className="center-parent">
                                        <div className="form-group">
                                            <select className="form-control" id="gender_select" onChange={handleChangeGender} value={gender}>
                                                <option value="1">Nam</option>
                                                <option value="2">N</option>
                                                <option value="3">Khc</option>
                                            </select>
                                            <small id="gender_help" className="form-text text-muted">* Nhn  chn.</small>
                                        </div>
                                    </Grid>
                                    <Grid item xs={4}>
                                        Tn nhn hng *
                                    </Grid>
                                    <Grid item xs={8} className="center-parent">
                                        <input 
                                            type="text" 
                                            className="form-control" 
                                            id="brandname" 
                                            aria-describedby="emailHelp" 
                                            placeholder="Tn nhn hng" 
                                            value={brandname}
                                            onChange = {handleChangeBrandname}
                                        />
                                    </Grid>
                                    <Grid item xs={4}>
                                        Tnh/ Thnh ph *
                                    </Grid>
                                    <Grid item xs={8} className="center-parent">
                                        <div className="form-group">
                                            <select className="form-control" id="province_select" onChange={handleChangeAddress} value={address}>
                                                {/* <option value="79">TP H Ch Minh</option>
                                                <option value="01">H Ni</option>
                                                <option value="48"> Nng</option>
                                                <option value="22">Qung Ninh</option>
                                                <option value="92">Cn Th</option>
                                                <option value="77">Vng Tu</option> */}
                                                {provinces.length>0 && provinces.map((list)=>(
                                                    <option key={list.id} value={list.id}>{list.name}</option>
                                                ))}

                                            </select>
                                            <small id="address" className="form-text text-muted">* Nhn  chn.</small>
                                        </div>
                                    </Grid>
                                </Grid>
                            </div>
                            <br />
                            <p className="forgot-password text-right"> <span>  Bng vic ng k ti khon, bn  ng  vi</span>
                                <Link to="/register" style={{ color: "#00B14F", textDecoration: "none" }}> iu khon dch v</Link>
                                <span> v</span> <Link to="/register" style={{ color: "#00B14F", textDecoration: "none" }}>  Chnh sch bo mt </Link>
                                <span>ca chng ti</span>
                            </p>
                            <div style={{textAlign:"center"}}>
                                <Button type="submit" fullWidth
                                    name="signin" id="signin" value="ng nhp" variant="contained"
                                    style={{ backgroundColor: "#00B14F", width: "auto" }}
                                    onClick={handleSubmit}>ng k
                                </Button>
                            </div>


                        </div>
                    </form>
                </div>
            </div>


        </div>
    )
}
